import React from 'react';

class TabComp extends React.PureComponent {
  onHandleChange = (index) => {
    const temp = index;
  }

  render() {
    const { selected, children } = this.props;
    return (
      <div>
        <ul className="inline">
          {children.map((elem, index) => {
            const style = index === selected ? 'selected' : '';
            return (
              <li className={style} key={elem.props.uniqueKey} onChange={this.onHandleChange(this, index)}> 
                {elem.props.title}
              </li>
            );
          })}
        </ul>
        TabComp 
        {selected}
        {/* {children.map((elem) => (
          console.log(elem.props.title)
            ))} */}
      </div>
    );
  }
}

export default TabComp;
